<template>
  <div class="row-2_left">
    <div class="general-title">黑马款</div>
    <div class="left-data">
      <lineChart v-for="(item, index) in tempData" :key="index" :propsData="item"/>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import lineChart from "./row2rightcomponents/lineChart.vue";

export default {
  name: "Row2Right",
  data() {
    return {};
  },
  components: {
    lineChart
  },
  computed: {
    tempData() {
      let data = this.$store.state.row2RightPublishData,
        tempArry = [];
      if (!this.$lodash.isEmpty(data)) {
        let tempObj = {},
          i = 0;
        data.forEach((element, index) => {
          let styleName = element.styleNumber;
          let styleExist = this.$lodash.has(tempObj, styleName);
          if (styleExist) {
            tempObj[styleName].push(element);
          } else {
            tempObj[styleName] = [element];
          }
        });
        this.$lodash.forIn(tempObj, (value, key) => {
          let noTurnoversInStorage = value.filter(
              item => item.type === "入库数无翻单"
            )[0],
            inventoryNumber1Inversion = value.filter(
              item => item.type === "入库数1次翻单"
            )[0],
            inventoryNumber2Inversion = value.filter(
              item => item.type === "入库数2次翻单"
            )[0],
            salesTurnover = value.filter(item => item.type === "销售数翻单")[0],
            noTurnoverInSales = value.filter(
              item => item.type === "销售数无翻单"
            )[0],
            noInventoryTurnoverAtTheEndOfMonth = value.filter(
              item => item.type === "月末库存无翻单"
            )[0],
            inventoryTurnover1AtTheEndOfMonth = value.filter(
              item => item.type === "月末库存1次翻单"
            )[0],
            inventoryTurnover2AtTheEndOfMonth = value.filter(
              item => item.type === "月末库存2次翻单"
            )[0];
          tempArry.push({
            id: ++i,
            styleNumber: key,
            ratio: `${value[0].ratio}`,
            data: [
              [
                noTurnoversInStorage.may,
                noTurnoversInStorage.june,
                noTurnoversInStorage.july,
                noTurnoversInStorage.august,
                noTurnoversInStorage.september,
                noTurnoversInStorage.october,
                noTurnoversInStorage.november,
                noTurnoversInStorage.december,
                noTurnoversInStorage.january,
                noTurnoversInStorage.february,
                noTurnoversInStorage.march
              ],
              [
                inventoryNumber1Inversion.may,
                inventoryNumber1Inversion.june,
                inventoryNumber1Inversion.july,
                inventoryNumber1Inversion.august,
                inventoryNumber1Inversion.september,
                inventoryNumber1Inversion.october,
                inventoryNumber1Inversion.november,
                inventoryNumber1Inversion.december,
                inventoryNumber1Inversion.january,
                inventoryNumber1Inversion.february,
                inventoryNumber1Inversion.march
              ],
              [
                inventoryNumber2Inversion.may,
                inventoryNumber2Inversion.june,
                inventoryNumber2Inversion.july,
                inventoryNumber2Inversion.august,
                inventoryNumber2Inversion.september,
                inventoryNumber2Inversion.october,
                inventoryNumber2Inversion.november,
                inventoryNumber2Inversion.december,
                inventoryNumber2Inversion.january,
                inventoryNumber2Inversion.february,
                inventoryNumber2Inversion.march
              ],
              [
                salesTurnover.may,
                salesTurnover.june,
                salesTurnover.july,
                salesTurnover.august,
                salesTurnover.september,
                salesTurnover.october,
                salesTurnover.november,
                salesTurnover.december,
                salesTurnover.january,
                salesTurnover.february,
                salesTurnover.march
              ],
              [
                noTurnoverInSales.may,
                noTurnoverInSales.june,
                noTurnoverInSales.july,
                noTurnoverInSales.august,
                noTurnoverInSales.september,
                noTurnoverInSales.october,
                noTurnoverInSales.november,
                noTurnoverInSales.december,
                noTurnoverInSales.january,
                noTurnoverInSales.february,
                noTurnoverInSales.march
              ],
              [
                noInventoryTurnoverAtTheEndOfMonth.may,
                noInventoryTurnoverAtTheEndOfMonth.june,
                noInventoryTurnoverAtTheEndOfMonth.july,
                noInventoryTurnoverAtTheEndOfMonth.august,
                noInventoryTurnoverAtTheEndOfMonth.september,
                noInventoryTurnoverAtTheEndOfMonth.october,
                noInventoryTurnoverAtTheEndOfMonth.november,
                noInventoryTurnoverAtTheEndOfMonth.december,
                noInventoryTurnoverAtTheEndOfMonth.january,
                noInventoryTurnoverAtTheEndOfMonth.february,
                noInventoryTurnoverAtTheEndOfMonth.march
              ],
              [
                inventoryTurnover1AtTheEndOfMonth.may,
                inventoryTurnover1AtTheEndOfMonth.june,
                inventoryTurnover1AtTheEndOfMonth.july,
                inventoryTurnover1AtTheEndOfMonth.august,
                inventoryTurnover1AtTheEndOfMonth.september,
                inventoryTurnover1AtTheEndOfMonth.october,
                inventoryTurnover1AtTheEndOfMonth.november,
                inventoryTurnover1AtTheEndOfMonth.december,
                inventoryTurnover1AtTheEndOfMonth.january,
                inventoryTurnover1AtTheEndOfMonth.february,
                inventoryTurnover1AtTheEndOfMonth.march
              ],
              [
                inventoryTurnover2AtTheEndOfMonth.may,
                inventoryTurnover2AtTheEndOfMonth.june,
                inventoryTurnover2AtTheEndOfMonth.july,
                inventoryTurnover2AtTheEndOfMonth.august,
                inventoryTurnover2AtTheEndOfMonth.september,
                inventoryTurnover2AtTheEndOfMonth.october,
                inventoryTurnover2AtTheEndOfMonth.november,
                inventoryTurnover2AtTheEndOfMonth.december,
                inventoryTurnover2AtTheEndOfMonth.january,
                inventoryTurnover2AtTheEndOfMonth.february,
                inventoryTurnover2AtTheEndOfMonth.march
              ]
            ]
          });
        });
        console.log(tempObj);
      }
      return tempArry;
    }
  },
  watch: {
    "$store.state.row2RightPublishData": {
      deep: true,
      handler(newValue, oldValue) {
        console.log(newValue);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.row-2_left {
  width: 603px;
  height: 323px;
  padding: 12px;
  background-color: #0a0819;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 7px;
  box-shadow: inset 0px 0px 20px #2b255f;
}
.general-title {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  border-left: 6px solid #3b54d3;
  padding-left: 12px;
}
.left-data {
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
</style>