<template>
  <div class="row-1_right">
    <div class="data-title">
      <div class="data-title_text">供应计划</div>
      <div class="data-legend">
        <div class="row-1-right_legend-1">实际入库</div>
        <div class="row-1-right_legend-2">计划入库</div>
      </div>
    </div>
    <div class="data-content">
      <treemapAndLineChart v-for="(item, index) in tempData" :key="index" :propsData="item"/>
    </div>
  </div>
</template>

<script>
import treemapAndLineChart from "./row1rightcomponents/treemapAndLineChart.vue";

export default {
  name: "Row1Right",
  data() {
    return {};
  },
  components: {
    treemapAndLineChart
  },
  computed: {
    tempData() {
      let data = this.$store.state.row1RightPublishData,
        tempArry = [];
      if (!this.$lodash.isEmpty(data)) {
        let tempObj = {},
          i = 0;
        data.forEach((element, index) => {
          let categoryName = element.category;
          let categoryExist = this.$lodash.has(tempObj, categoryName);
          if (categoryExist) {
            tempObj[categoryName].push(element);
          } else {
            tempObj[categoryName] = [element];
          }
        });
        this.$lodash.forIn(tempObj, (value, key) => {
          let plannedStorage = value.filter(
              item => item.type === "计划入库"
            )[0],
            actualStorage = value.filter(item => item.type === "实际入库")[0];
          tempArry.push({
            name: key,
            id: ++i,
            data: [
              {
                name: "计划入库",
                value: [
                  plannedStorage.june,
                  plannedStorage.july,
                  plannedStorage.august,
                  plannedStorage.september,
                  plannedStorage.october,
                  plannedStorage.november,
                  plannedStorage.december
                ]
              },
              {
                name: "实际入库",
                value: [
                  actualStorage.june,
                  actualStorage.july,
                  actualStorage.august,
                  actualStorage.september,
                  actualStorage.october,
                  actualStorage.november,
                  actualStorage.december
                ]
              }
            ]
          });
        });
        console.log(tempArry);
      }
      return tempArry;
    }
  },
  watch: {
    // "$store.state.row1RightPublishData": {
    //   deep: true,
    //   handler(newValue, oldValue) {
    //     console.log(newValue);
    //   }
    // }
  }
};
</script>

<style lang="scss" scoped>
.row-1_right {
  width: 603px;
  height: 323px;
  padding: 12px;
  background-color: #0a0819;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  //   border: 1px solid #100b30;
  // opacity: 0.8;
  border-radius: 7px;
  box-shadow: inset 0px 0px 20px #2b255f;
}
.data-title {
  // margin: 12px 12px 0 12px;
  display: flex;
  justify-content: space-between;
}
.data-title_text {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  border-left: 6px solid #3b54d3;
  padding-left: 12px;
}
.data-legend {
  display: flex;
  justify-content: center;
}
.row-1-right_legend-1 {
  font-size: 16px;
  font-weight: 400;
  color: #7c83a8;
  margin-left: 48px;
  position: relative;
}
.row-1-right_legend-1::before {
  width: 13px;
  height: 0;
  border-top: 4px solid #3b8bff;
  position: absolute;
  content: "";
  top: 8px;
  left: -20px;
}
.row-1-right_legend-2 {
  font-size: 16px;
  font-weight: 400;
  color: #7c83a8;
  margin-left: 48px;
  position: relative;
}
.row-1-right_legend-2::before {
  width: 13px;
  height: 0;
  border-top: 4px solid #fcc63f;
  position: absolute;
  content: "";
  top: 8px;
  left: -20px;
}
.data-content {
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
</style>