<template>
  <el-container>
    <el-header>
      <el-upload
        name="file"
        action
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
        :multiple="false"
        :show-file-list="false"
        :on-change="handleChange"
        :disabled="uploadStatus"
      >
        <el-button
          class="margin-right"
          type="primary"
          round
          size="small"
          icon="el-icon-upload"
          :loading="uploadStatus"
        >上传Excel</el-button>
      </el-upload>
      <el-button @click="addData" type="success" round size="small" icon="el-icon-circle-plus">新增数据</el-button>
      <el-button
        @click="releaseData('ENSURE_ROW3LEFTPUBLISHDATA')"
        type="warning"
        round
        size="small"
        icon="el-icon-s-opportunity"
      >发布数据</el-button>
      <el-button
        @click="wipeData('ENSURE_EMPTY_ROW3LEFTDATA')"
        type="danger"
        round
        size="small"
        icon="el-icon-delete-solid"
      >清空数据</el-button>
    </el-header>
    <el-main>
      <el-table :data="tableData" border max-height="595">
        <el-table-column fixed label="款号">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.title" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.title}}</span>
          </template>
        </el-table-column>
        <el-table-column label="库存">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.inventory" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.inventory}}</span>
          </template>
        </el-table-column>
        <el-table-column label="销售">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.sales" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.sales}}</span>
          </template>
        </el-table-column>
        <el-table-column label="售罄率">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.sellThroughRate" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.sellThroughRate}}</span>
          </template>
        </el-table-column>
        <el-table-column label="促销方式">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.promotionMethods" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.promotionMethods}}</span>
          </template>
        </el-table-column>
        <el-table-column label="滞销情况">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.unmarketableSituation" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.unmarketableSituation}}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <a class="save-button" @click="save(scope.row.id)">保存</a>
              <a class="cancel-button" @click="cancel(scope.row.id)">取消</a>
            </span>
            <el-button v-else size="mini" @click="handleEdit(scope.row.id)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalData"
      ></el-pagination>-->
    </el-main>
  </el-container>
</template>

<script>
import maintain from "@/mixins/maintain";

export default {
  name: "maintainRow1Left",
  mixins: [maintain],
  data() {
    return {
      attrCorrespondence: {
        款号: "title",
        库存: "inventory",
        销售: "sales",
        售罄率: "sellThroughRate",
        促销方式: "promotionMethods",
        滞销情况: "unmarketableSituation"
      },
      totalData: 50,
      initData: [],
      tableData: []
    };
  },
  computed: {},
  watch: {
    initData: {
      deep: true,
      handler(newValue, oldValue) {
        let returnData = [];
        if (!this.$lodash.isEmpty(newValue)) {
          newValue.forEach((item, index) => {
            let tempObj = {};
            this.$lodash.forIn(item, (value, key) => {
              if (this.attrCorrespondence[key.trim()].indexOf("sell") > -1) {
                tempObj[
                  this.attrCorrespondence[key.trim()]
                ] = this.$lodash.round(value, 2);
              } else {
                tempObj[this.attrCorrespondence[key.trim()]] = value;
              }
            });
            this.$store.state.row3LeftCount++;
            tempObj.id = this.$store.state.row3LeftCount;
            returnData.push(tempObj);
          });
        }
        console.log(returnData);
        this.tableData = this.tableData.concat(returnData);
        this.cacheData = this.tableData.map(item => ({ ...item }));
      }
    },
    tableData: {
      deep: true,
      handler(newValue, oldValue) {
        // this.cacheData = newValue.map(item => ({ ...item }));
        this.$store.state.row3LeftBackupData = newValue;
      }
    }
  },
  methods: {
    addData() {
      this.$store.state.row3LeftCount++;
      this.tableData.push({
        id: this.$store.state.row3LeftCount,
        title: "请修改",
        inventory: "请修改",
        sales: "请修改",
        sellThroughRate: "请修改",
        promotionMethods: "请修改",
        unmarketableSituation: "请修改"
      });
      this.cacheData = this.tableData.map(item => ({ ...item }));
    }
  }
};
</script>

<style lang="scss" scoped>
.el-header {
  background-color: #fafafa;
  border: 1px solid #eaeefb;
  color: #333;
  text-align: center;
  display: flex;
  align-items: center;
}
.el-main {
  background-color: #fafafa;
  color: #333;
  text-align: center;
}
.margin-right {
  margin-right: 10px;
}
.save-button,
.cancel-button {
  color: #1890ff;
  cursor: pointer;
  padding: 0 5px;
  font-size: 12px;
}
</style>