<template>
  <el-container>
    <el-header>
      <el-upload
        name="file"
        action
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
        :multiple="false"
        :show-file-list="false"
        :on-change="handleChange"
        :disabled="uploadStatus"
      >
        <el-button
          class="margin-right"
          type="primary"
          round
          size="small"
          icon="el-icon-upload"
          :loading="uploadStatus"
        >上传Excel</el-button>
      </el-upload>
      <el-button @click="addData" type="success" round size="small" icon="el-icon-circle-plus">新增数据</el-button>
      <el-button
        @click="releaseData('ENSURE_ROW3RIGHTPUBLISHDATA')"
        type="warning"
        round
        size="small"
        icon="el-icon-s-opportunity"
      >发布数据</el-button>
      <el-button
        @click="wipeData('ENSURE_EMPTY_ROW3RIGHTDATA')"
        type="danger"
        round
        size="small"
        icon="el-icon-delete-solid"
      >清空数据</el-button>
    </el-header>
    <el-main>
      <el-table :data="tableData" border max-height="595">
        <el-table-column fixed label="款号">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.styleNumber" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.styleNumber}}</span>
          </template>
        </el-table-column>
        <el-table-column label="销售">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.sell" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.sell}}</span>
          </template>
        </el-table-column>
        <el-table-column label="库存">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.stock" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.stock}}</span>
          </template>
        </el-table-column>
        <el-table-column label="售罄率">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.soldOutRate" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.soldOutRate}}</span>
          </template>
        </el-table-column>
        <el-table-column label="试销期">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.salesPeriod" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.salesPeriod}}</span>
          </template>
        </el-table-column>
        <el-table-column label="生产回货周期">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.returnCycle" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.returnCycle}}</span>
          </template>
        </el-table-column>
        <el-table-column label="预计交期">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.expectedDelivery" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.expectedDelivery}}</span>
          </template>
        </el-table-column>
        <el-table-column label="最晚上市">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.latestTimeMarket" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.latestTimeMarket}}</span>
          </template>
        </el-table-column>
        <el-table-column label="需求量">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.requirement" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.requirement}}</span>
          </template>
        </el-table-column>
        <el-table-column label="订单">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.orderQuantity" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.orderQuantity}}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <a class="save-button" @click="save(scope.row.id)">保存</a>
              <a class="cancel-button" @click="cancel(scope.row.id)">取消</a>
            </span>
            <el-button v-else size="mini" @click="handleEdit(scope.row.id)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalData"
      ></el-pagination>-->
    </el-main>
  </el-container>
</template>

<script>
import maintain from "@/mixins/maintain";

export default {
  name: "maintainRow3Right",
  mixins: [maintain],
  data() {
    return {
      attrCorrespondence: {
        款号: "styleNumber",
        销售: "sell",
        库存: "stock",
        售罄率: "soldOutRate",
        试销期: "salesPeriod",
        生产回货周期: "returnCycle",
        预计交期: "expectedDelivery",
        最晚上市: "latestTimeMarket",
        需求量: "requirement",
        订单: "orderQuantity"
      },
      totalData: 50,
      initData: [],
      tableData: []
    };
  },
  computed: {},
  watch: {
    initData: {
      deep: true,
      handler(newValue, oldValue) {
        let returnData = [];
        if (!this.$lodash.isEmpty(newValue)) {
          newValue.forEach((item, index) => {
            let tempObj = {};
            this.$lodash.forIn(item, (value, key) => {
              if (this.attrCorrespondence[key.trim()].indexOf("sold") > -1) {
                tempObj[
                  this.attrCorrespondence[key.trim()]
                ] = this.$lodash.round(value, 2);
              } else {
                tempObj[
                  this.attrCorrespondence[key.trim()]
                ] = this.$lodash.isNumber(value)
                  ? this.$lodash.round(value)
                  : value;
              }
            });
            this.$store.state.row3RightCount++;
            tempObj.id = this.$store.state.row3RightCount;
            returnData.push(tempObj);
          });
        }
        console.log(returnData);
        this.tableData = this.tableData.concat(returnData);
        this.cacheData = this.tableData.map(item => ({ ...item }));
      }
    },
    tableData: {
      deep: true,
      handler(newValue, oldValue) {
        // this.cacheData = newValue.map(item => ({ ...item }));
        this.$store.state.row3RightBackupData = newValue;
      }
    }
  },
  methods: {
    addData() {
      this.$store.state.row3RightCount++;
      this.tableData.push({
        id: this.$store.state.row3RightCount,
        styleNumber: "请修改",
        sell: "请修改",
        stock: "请修改",
        soldOutRate: "请修改",
        salesPeriod: "请修改",
        returnCycle: "请修改",
        expectedDelivery: "请修改",
        latestTimeMarket: "请修改",
        requirement: "请修改",
        orderQuantity: "请修改"
      });
      this.cacheData = this.tableData.map(item => ({ ...item }));
    }
  }
};
</script>

<style lang="scss" scoped>
.el-header {
  background-color: #fafafa;
  border: 1px solid #eaeefb;
  color: #333;
  text-align: center;
  display: flex;
  align-items: center;
}
.el-main {
  background-color: #fafafa;
  color: #333;
  text-align: center;
}
.margin-right {
  margin-right: 10px;
}
.save-button,
.cancel-button {
  color: #1890ff;
  cursor: pointer;
  padding: 0 5px;
  font-size: 12px;
}
</style>