<script>
import maintain from "@/mixins/maintain";

export default {
  name: "maintainRow1Mid",
  mixins: [maintain],
  data() {
    return {
      attrCorrespondence: {
        类型: "type",
        今年计划: "yearPlan",
        今年实际: "yearActual",
        去年同期: "lastYearSimultaneously",
        "6月1": "data1",
        "6月15": "data2",
        "7月1": "data3",
        "7月15": "data4",
        "8月1": "data5",
        "8月15": "data6",
        "9月1": "data7",
        "9月15": "data8",
        "10月1": "data9",
        "10月15": "data10",
        "11月1": "data11",
        "11月15": "data12",
        "12月1": "data13",
        "12月15": "data14",
        "1月1": "data15",
        "1月15": "data16",
        "2月1": "data17",
        "2月15": "data18",
        "3月1": "data19",
        "3月15": "data20",
        "3月30": "data21"
      },
      totalData: 50,
      initData: [],
      tableData: []
    };
  },
  computed: {},
  watch: {
    initData: {
      deep: true,
      handler(newValue, oldValue) {
        let returnData = [];
        if (!this.$lodash.isEmpty(newValue)) {
          newValue.forEach((item, index) => {
            let tempObj = {};
            this.$lodash.forIn(item, (value, key) => {
              tempObj[this.attrCorrespondence[key.trim()]] = value;
            });
            this.$store.state.row1MidCount++;
            tempObj.id = this.$store.state.row1MidCount;
            returnData.push(tempObj);
          });
        }
        console.log(returnData);
        this.tableData = this.tableData.concat(returnData);
        this.cacheData = this.tableData.map(item => ({ ...item }));
      }
    },
    tableData: {
      deep: true,
      handler(newValue, oldValue) {
        // this.cacheData = newValue.map(item => ({ ...item }));
        this.$store.state.row1MidBackupData = newValue;
      }
    }
  },
  methods: {
    addData() {
      this.$store.state.row1MidCount++;
      this.tableData.push({
        id: this.$store.state.row1MidCount,
        type: "请修改",
        yearPlan: "请修改",
        yearActual: "请修改",
        lastYearSimultaneously: "请修改",
        data1: "请修改",
        data2: "请修改",
        data3: "请修改",
        data4: "请修改",
        data5: "请修改",
        data6: "请修改",
        data7: "请修改",
        data8: "请修改",
        data9: "请修改",
        data10: "请修改",
        data11: "请修改",
        data12: "请修改",
        data13: "请修改",
        data14: "请修改",
        data15: "请修改",
        data16: "请修改",
        data17: "请修改",
        data18: "请修改",
        data19: "请修改",
        data20: "请修改",
        data21: "请修改"
      });
      this.cacheData = this.tableData.map(item => ({ ...item }));
    }
  }
};
</script>

