<template>
  <div class="row-3_mid">
    <div class="general-title">生产监控</div>
    <div class="row-3_mid-table">
      <div class="mid-table_left">
        <div class="table_left-title">新增订单</div>
        <table class="order-table">
          <thead>
            <tr class="table-header">
              <th>订单号</th>
              <th>商品编号</th>
              <th>数量</th>
              <th>跟单员</th>
            </tr>
          </thead>
          <tbody>
            <tr class="table-body" v-for="(item ,index) in tableLeftData" :key="`left${index}`">
              <td>{{item.orderNumber}}</td>
              <td>{{item.commodityNumber}}</td>
              <td>{{item.number}}</td>
              <td>{{item.tracker}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="mid-table_left">
        <div class="table_left-title">在制订单</div>
        <table class="order-table">
          <thead>
            <tr class="table-header">
              <th>订单号</th>
              <th>商品编号</th>
              <th>数量</th>
              <th>跟单员</th>
            </tr>
          </thead>
          <tbody>
            <tr class="table-body" v-for="(item ,index) in tableRightData" :key="`right${index}`">
              <td>{{item.orderNumber}}</td>
              <td>{{item.commodityNumber}}</td>
              <td>{{item.number}}</td>
              <td>{{item.tracker}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div class="row-3_mid-chart">
      <div class="mid-chart_pie" id="row3midpieleft"></div>
      <div class="mid-chart_line" id="row3midlinemixbar"></div>
      <div class="mid-chart_pie" id="row3midpieright"></div>
      <div class="mid-chart_line" id="row3midline"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Row3Mid",
  data() {
    return {};
  },
  computed: {
    tableLeftData() {
      console.log(this.$store.state.row3MidPublishData);
      return this.$store.state.row3MidPublishData.filter(
        item => item.status == "新增"
      );
    },
    tableRightData() {
      console.log(this.$store.state.row3MidPublishData);
      return this.$store.state.row3MidPublishData.filter(
        item => item.status == "在制"
      );
    }
  },
  mounted() {
    this.renderPieLeft();
    this.renderLineMixBar();
    this.renderPieRight();
    this.renderLine();
  },
  methods: {
    renderPieLeft() {
      var myPie = this.$echarts.init(document.getElementById("row3midpieleft"));
      var option = {
        series: [
          {
            name: "leftpie",
            type: "pie",
            radius: ["70%", "100%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center",
                textStyle: {
                  fontSize: "12"
                }
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "12",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: 70,
                name: "翻单比例",
                label: {
                  show: true,
                  color: "#1CC9E8",
                  // formatter: "{d}% \n {b}",
                  formatter: ["{a|{d}%}", "{b|{b}}"].join("\n"),
                  rich: {
                    a: {
                      color: "#1CC9E8",
                      fontSize: 14,
                      fontFamily: "digital"
                      // lineHeight: 10
                    },
                    b: {
                      color: "#7C83A8",
                      fontSize: 12
                      // height: 40
                    }
                  }
                },
                itemStyle: {
                  color: "#3B54D3"
                }
              },
              {
                value: 30,
                name: "leftpie",
                itemStyle: {
                  color: "#0a091a",
                  borderColor: "#243079"
                }
              }
            ]
          }
        ]
      };
      myPie.setOption(option, true);
    },
    renderPieRight() {
      var myPie = this.$echarts.init(
        document.getElementById("row3midpieright")
      );
      var option = {
        // legend: {
        //   orient: "vertical",
        //   x: "left",
        //   data: ["直接访问", "邮件营销", "联盟广告", "视频广告", "搜索引擎"]
        // },
        series: [
          {
            name: "rightpie",
            type: "pie",
            radius: ["70%", "100%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center",
                textStyle: {
                  fontSize: "12"
                }
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "12",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: 70,
                name: "在制比例",
                label: {
                  show: true,
                  color: "#1CC9E8",
                  // formatter: "{d}% \n {b}",
                  formatter: ["{a|{d}%}", "{b|{b}}"].join("\n"),
                  rich: {
                    a: {
                      color: "#1CC9E8",
                      fontSize: 14,
                      fontFamily: "digital"
                      // lineHeight: 10
                    },
                    b: {
                      color: "#7C83A8",
                      fontSize: 12
                      // height: 40
                    }
                  }
                },
                itemStyle: {
                  color: "#3B54D3"
                }
              },
              {
                value: 30,
                name: "rightpie",
                itemStyle: {
                  color: "#0a091a",
                  borderColor: "#243079"
                }
              }
            ]
          }
        ]
      };
      myPie.setOption(option, true);
    },
    renderLine() {
      var myPie = this.$echarts.init(document.getElementById("row3midline"));
      var option = {
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "1%",
          right: "1%",
          top: "4%",
          bottom: "1%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          data: ["1/1", "2/1", "3/1", "4/1", "5/1", "6/1", "7/1"],
          axisTick: {
            show: false
          },
          axisLabel: {
            color: "#3A3E5E"
          },
          axisLine: {
            lineStyle: {
              color: "#120F29",
              fontWeight: "400"
            }
          },
          splitLine: {
            show: true,
            interval: 0,
            lineStyle: {
              color: "#120F29",
              width: 0.5
            }
          }
        },
        yAxis: {
          type: "value",
          nameLocation: "end",
          nameGap: 5,
          nameTextStyle: {
            color: "#3A3E5E"
          },
          axisLine: {
            lineStyle: {
              color: "#120F29"
            }
          },
          splitLine: {
            show: false,
            lineStyle: {
              color: "#120F29"
            }
          },
          min: 0,
          max: 250,
          interval: 50,
          axisTick: {
            show: false
          },
          axisLabel: {
            margin: 2,
            color: "#3A3E5E",
            fontWeight: "400"
          }
        },
        series: [
          {
            name: "销售",
            type: "line",
            lineStyle: {
              color: "#3B8BFF"
            },
            itemStyle: {
              color: "#ffffff",
              borderColor: "#3B8BFF"
            },
            data: [100, 150, 100, 150, 100, 150, 100]
          },
          {
            name: "折扣",
            type: "line",
            lineStyle: {
              color: "#FCC63F"
            },
            itemStyle: {
              color: "#ffffff",
              borderColor: "#FCC63F"
            },
            data: [120, 140, 120, 140, 120, 140, 120]
          }
        ]
      };
      myPie.setOption(option, true);
    },
    renderLineMixBar() {
      var myPie = this.$echarts.init(
        document.getElementById("row3midlinemixbar")
      );
      var option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        grid: {
          left: "1%",
          right: "1%",
          top: "4%",
          bottom: "1%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: ["1/1", "2/1", "3/1", "4/1", "5/1", "6/1", "7/1"],
            axisPointer: {
              type: "shadow"
            },
            axisTick: {
              show: false
            },
            axisLabel: {
              color: "#3A3E5E"
            },
            axisLine: {
              lineStyle: {
                color: "#120F29"
              }
            },
            splitLine: {
              show: true,
              interval: 0,
              lineStyle: {
                color: "#120F29",
                width: 0.5
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "水量",
            splitLine: {
              show: false,
              lineStyle: {
                color: "#120F29"
              }
            },
            axisLine: {
              lineStyle: {
                color: "#120F29"
              }
            },
            axisTick: {
              show: false
            },
            min: 0,
            max: 250,
            interval: 50,
            axisLabel: {
              margin: 2,
              color: "#3A3E5E",
              fontWeight: "400"
            }
          },
          {
            type: "value",
            name: "温度",
            splitLine: {
              show: false,
              lineStyle: {
                color: "#120F29"
              }
            },
            axisLine: {
              lineStyle: {
                color: "#120F29"
              }
            },
            axisTick: {
              show: false
            },
            min: 0,
            max: 25,
            interval: 5,
            axisLabel: {
              margin: 2,
              color: "#3A3E5E",
              fontWeight: "400"
            }
          }
        ],
        series: [
          {
            name: "蒸发量",
            type: "bar",
            itemStyle: {
              color: "#3B8BFF"
            },
            data: [2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6]
          },
          {
            name: "平均温度",
            type: "line",
            lineStyle: {
              color: "#FCC63F"
            },
            itemStyle: {
              color: "#ffffff",
              borderColor: "#FCC63F"
            },
            yAxisIndex: 1,
            data: [2.0, 2.2, 3.3, 4.5, 6.3, 10.2, 20.3]
          }
        ]
      };
      myPie.setOption(option, true);
    }
  }
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "digital";
  src: url("../assets/digital-7.ttf");
}
.row-3_mid {
  width: 603px;
  height: 323px;
  padding: 12px;
  background-color: #0a0819;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  //   border: 1px solid #100b30;
  // opacity: 0.8;
  border-radius: 7px;
  box-shadow: inset 0px 0px 20px #2b255f;
}
.general-title {
  // margin: 12px 12px 0 12px;
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  border-left: 6px solid #3b54d3;
  padding-left: 12px;
}
.row-3_mid-table {
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
}
.mid-table_left {
  width: 300px;
}
.table_left-title {
  height: 20px;
  background: #0e0e25;
  font-size: 12px;
  line-height: 22px;
  font-weight: 400;
  color: #7c83a8;
  text-align: center;
}
.order-table {
  margin-top: 6px;
  width: 100%;
  border: none;
  border-spacing: 0;
  text-align: center;
}
.table-header {
  height: 30px;
  background-color: #0f0c24;
  color: #3b54d3;
  font-size: 12px;
  font-weight: 400;
}
.table-body {
  height: 30px;
  color: #7c83a8;
  font-size: 12px;
  font-weight: 400;
}
.row-3_mid-chart {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mid-chart_pie {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  // background-color: #448de6;
}
.mid-chart_line {
  width: 185px;
  height: 105px;
  // background-color: #120f29;
}
</style>