<template>
  <div class="row-2_mid">
    <div class="general-title">供应计划</div>
    <div class="data-content">
      <orderBar v-for="(item, index) in tempData" :key="index" :propsData="item"/>
    </div>
  </div>
</template>

<script>
import orderBar from "./row2midcomponents/orderBar.vue";

export default {
  name: "Row2Mid",
  data() {
    return {};
  },
  components: {
    orderBar
  },
  mounted() {
    // this.getTimeSpan("weekly");
  },
  computed: {
    tempData() {
      let data = this.$store.state.row2MidPublishData,
        tempArry = [];
      if (!this.$lodash.isEmpty(data)) {
        let templateData = [];
        data.forEach((element, index) => {
          templateData.push({
            orderNumber: element.orderNumber,
            predictStartTime: element.predictStartTime,
            predictEndTime: element.predictEndTime,
            actualStartTime: element.actualStartTime,
            actualEndTime: element.actualEndTime,
            orderStatus: element.orderStatus,
            index: element.index
          });
        });
        tempArry = [
          {
            name: "全季供应计划",
            flag: 1,
            id: 1,
            timespan: this.getTimeSpan("quarterly"),
            data: templateData
          },
          {
            name: "月供应计划",
            flag: 2,
            id: 2,
            timespan: this.getTimeSpan("monthly"),
            data: templateData
          },
          {
            name: "周供应计划",
            flag: 3,
            id: 3,
            timespan: this.getTimeSpan("weekly"),
            data: templateData
          }
        ];
      }
      return tempArry;
    }
  },
  watch: {
    // "$store.state.row2MidPublishData": {
    //   deep: true,
    //   handler(newValue, oldValue) {
    //     console.log(newValue);
    //   }
    // }
  },
  methods: {
    getTimeSpan(type) {
      let temparry = [],
        today = this.$moment();
      if (type == "quarterly") {
        let currentMonth = this.$moment(today).format("YYYY-MM");
        for (let i = 0; i < 9; i++) {
          temparry.push({
            name: this.$moment(currentMonth)
              .add(i, "months")
              .format("Mo"),
            startDay: this.$moment(currentMonth)
              .add(i, "months")
              .startOf("month")
              .format("YYYY-MM-DD HH:mm:ss"),
            endDay: this.$moment(currentMonth)
              .add(i, "months")
              .endOf("month")
              .format("YYYY-MM-DD HH:mm:ss")
          });
        }
      } else if (type == "monthly") {
        let monthStartDay = this.$moment(today)
          .startOf("month")
          .format("YYYY-MM-DD");
        let monthEndDay = this.$moment(today)
          .endOf("month")
          .format("YYYY-MM-DD");
        let i = 0,
          tempDay = "";
        do {
          tempDay = this.$moment(monthStartDay)
            .add(i, "days")
            .format("YYYY-MM-DD");
          temparry.push({
            name: this.$moment(tempDay).format("D"),
            startDay: this.$moment(tempDay)
              .startOf("days")
              .format("YYYY-MM-DD HH:mm:ss"),
            endDay: this.$moment(tempDay)
              .endOf("days")
              .format("YYYY-MM-DD HH:mm:ss")
          });
          i++;
        } while (tempDay != monthEndDay);
      } else if (type == "weekly") {
        let weekStartDay = this.$moment(today)
          .startOf("week")
          .format("YYYY-MM-DD");
        let weekEndDay = this.$moment(today)
          .endOf("week")
          .format("YYYY-MM-DD");
        let i = 0,
          tempDay = "";
        do {
          tempDay = this.$moment(weekStartDay)
            .add(i, "days")
            .format("YYYY-MM-DD");
          temparry.push({
            name: this.$moment(tempDay).format("dddd"),
            startDay: this.$moment(tempDay)
              .startOf("days")
              .format("YYYY-MM-DD HH:mm:ss"),
            endDay: this.$moment(tempDay)
              .endOf("days")
              .format("YYYY-MM-DD HH:mm:ss")
          });
          i++;
        } while (tempDay != weekEndDay);
      }
      // console.log(temparry);
      return temparry;
    }
  }
};
</script>

<style lang="scss" scoped>
.row-2_mid {
  width: 603px;
  height: 323px;
  padding: 12px;
  background-color: #0a0819;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 7px;
  box-shadow: inset 0px 0px 20px #2b255f;
}
.general-title {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  border-left: 6px solid #3b54d3;
  padding-left: 12px;
}
.data-content {
  margin-top: 12px;
}
</style>