import XLSX from "xlsx";

export default {
    data() {
        return {
            uploadStatus: false,
            pageSize: 10,
            currentPage: 1,
            cacheData: [],
        }
    },
    mounted() {
        // this.cacheData = this.tableData.map(item => ({ ...item }));
    },
    methods: {
        releaseData(name) {
            console.log(`发布数据：${name}`);
            this.$store.dispatch(name).then(() => {
                this.$message({
                    message: `发布数据成功！`,
                    type: "success",
                    showClose: true,
                    duration: 2000
                });
            });
        },
        wipeData(name) {
            this.$confirm("清空数据将无法恢复, 是否执行清空?", "提示", {
                confirmButtonText: "清空",
                cancelButtonText: "取消",
                type: "warning"
            })
                .then(() => {
                    this.tableData = [];
                    this.cacheData = [];
                    this.$store.dispatch(name);
                })
                .catch(() => {
                    console.log(`点取消`);
                });
        },
        handleChange(file, fileList) {
            this.uploadStatus = true;
            if (file.status == "fail") {
                this.$message({
                    message: `上传 ${file.name} 文件成功！`,
                    type: "success",
                    showClose: true,
                    duration: 2000
                });
                const fileReader = new FileReader();
                fileReader.onload = ev => {
                    try {
                        const data = ev.target.result;
                        // const workbook = XLSX.read(data, {
                        //     type: "binary"
                        // });
                        const workbook = XLSX.read(data, {type: "binary", cellDates:true});
                        const wsname = workbook.SheetNames[0]; //取第一张表
                        const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]); //生成json表格内容
                        console.log(ws)
                        this.initData = ws;
                    } catch (e) {
                        this.$message({
                            message: `解析 ${file.name} 文件失败！`,
                            type: "warning",
                            showClose: true,
                            duration: 2000
                        });
                        return false;
                    }
                    this.uploadStatus = false;
                };
                fileReader.readAsBinaryString(file.raw);
            }
        },
        handleEdit(id) {
            const newData = [...this.tableData];
            const target = newData.filter(item => id === item.id)[0];
            if (target) {
                target.editable = true;
                this.tableData = newData;
            }
        },
        handleDelete(id) {
            const dataSource = [...this.tableData];
            this.tableData = dataSource.filter(item => item.id !== id);
        },
        save(id) {
            const newData = [...this.tableData];
            const target = newData.filter(item => id === item.id)[0];
            if (target) {
                delete target.editable;
                this.tableData = newData;
                Object.assign(this.cacheData.filter(item => id === item.id)[0], target);
            }
        },
        cancel(id) {
            const newData = [...this.tableData];
            const target = newData.filter(item => id === item.id)[0];
            if (target) {
                Object.assign(target, this.cacheData.filter(item => id === item.id)[0]);
                delete target.editable;
                this.tableData = newData;
            }
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pageSize = val;
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currentPage = val;
        }
    }
}
