<template>
  <div class="content_row-2_item">
    <div class="row-2_item-pie" :id="piechartid"></div>
    <span class="row-2_item-title">{{piechartname}}</span>
  </div>
</template>

<script>
export default {
  name: "pieChart",
  props: ["propsData"],
  computed: {
    piechartid: function() {
      return `row1midpie${this.propsData.id}`;
    },
    piechartname: function() {
      return this.propsData.name;
    },
    piechartdata: function() {
      return this.propsData.data;
    }
  },
  data() {
    return {
      renderChartFlag: true
    };
  },
  mounted() {},
  watch: {
    propsData: {
      deep: true,
      handler(newValue, oldValue) {
        this.renderChartFlag = true;
      }
    },
    "$store.state.currentComponent": {
      deep: true,
      handler(newValue, oldValue) {
        if (newValue == "Home" && this.renderChartFlag)
          this.renderPie(this.piechartid);
      }
    }
  },
  methods: {
    renderPie(chartid) {
      var myPie = this.$echarts.init(document.getElementById(chartid));
      var option = {
        // legend: {
        //   orient: "vertical",
        //   x: "left",
        //   data: ["直接访问", "邮件营销", "联盟广告", "视频广告", "搜索引擎"]
        // },
        series: [
          {
            name: `${this.piechartname}`,
            type: "pie",
            radius: ["70%", "100%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "inner",
                textStyle: {
                  fontSize: "10"
                }
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "10",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: this.piechartdata[0].value[0],
                name: `${this.piechartdata[0].name}`,
                label: {
                  formatter: "{d}%",
                  show: true,
                  color: "#96A1DE"
                },
                itemStyle: {
                  color: "#8F48FF"
                }
              },
              {
                value: this.piechartdata[0].value[1],
                name: "空白",
                itemStyle: {
                  color: "#0a091a"
                }
              }
            ]
          },
          {
            name: `${this.piechartname}`,
            type: "pie",
            radius: ["35%", "65%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "inner",
                textStyle: {
                  fontSize: "10"
                }
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "10",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: this.piechartdata[1].value[0],
                name: `${this.piechartdata[1].name}`,
                label: {
                  formatter: "{d}%",
                  show: true,
                  color: "#96A1DE"
                },
                itemStyle: {
                  color: "#FCC63F"
                }
              },
              {
                value: this.piechartdata[1].value[1],
                name: "空白",
                itemStyle: {
                  color: "#0a091a"
                }
              }
            ]
          },
          {
            name: `${this.piechartname}`,
            type: "pie",
            radius: ["0%", "30%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "inner",
                textStyle: {
                  fontSize: "10"
                }
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "10",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: this.piechartdata[2].value[0],
                name: `${this.piechartdata[2].name}`,
                label: {
                  formatter: "{d}%",
                  show: true,
                  color: "#96A1DE"
                },
                itemStyle: {
                  color: "#3B8BFF"
                }
              },
              {
                value: this.piechartdata[2].value[1],
                name: "空白",
                itemStyle: {
                  color: "#0a091a"
                }
              }
            ]
          }
        ]
      };
      myPie.setOption(option, true);
      this.renderChartFlag = false;
    }
  }
};
</script>

<style lang="scss" scoped>
.content_row-2_item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.row-2_item-pie {
  width: 110px;
  height: 110px;
  //   background-color: #100b30;
  // border-radius: 50%;
}
.row-2_item-title {
  margin-top: 12px;
  font-size: 14px;
  font-weight: 400;
  color: #6c76aa;
}
</style>