<template>
  <el-container>
    <el-header>
      <el-upload
        name="file"
        action
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
        :multiple="false"
        :show-file-list="false"
        :on-change="handleChange"
        :disabled="uploadStatus"
      >
        <el-button
          class="margin-right"
          type="primary"
          round
          size="small"
          icon="el-icon-upload"
          :loading="uploadStatus"
        >上传Excel</el-button>
      </el-upload>
      <el-button @click="addData" type="success" round size="small" icon="el-icon-circle-plus">新增数据</el-button>
      <el-button
        @click="releaseData('ENSURE_ROW1RIGHTPUBLISHDATA')"
        type="warning"
        round
        size="small"
        icon="el-icon-s-opportunity"
      >发布数据</el-button>
      <el-button
        @click="wipeData('ENSURE_EMPTY_ROW1RIGHTDATA')"
        type="danger"
        round
        size="small"
        icon="el-icon-delete-solid"
      >清空数据</el-button>
    </el-header>
    <el-main>
      <el-table :data="tableData" border max-height="595">
        <el-table-column fixed label="类型">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.type" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.type}}</span>
          </template>
        </el-table-column>
        <el-table-column label="品类">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.category" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.category}}</span>
          </template>
        </el-table-column>
        <el-table-column label="6月">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.june" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.june}}</span>
          </template>
        </el-table-column>
        <el-table-column label="7月">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.july" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.july}}</span>
          </template>
        </el-table-column>
        <el-table-column label="8月">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.august" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.august}}</span>
          </template>
        </el-table-column>
        <el-table-column label="9月">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.september" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.september}}</span>
          </template>
        </el-table-column>
        <el-table-column label="10月">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.october" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.october}}</span>
          </template>
        </el-table-column>
        <el-table-column label="11月">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.november" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.november}}</span>
          </template>
        </el-table-column>
        <el-table-column label="12月">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.december" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.december}}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <a class="save-button" @click="save(scope.row.id)">保存</a>
              <a class="cancel-button" @click="cancel(scope.row.id)">取消</a>
            </span>
            <el-button v-else size="mini" @click="handleEdit(scope.row.id)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalData"
      ></el-pagination>-->
    </el-main>
  </el-container>
</template>

<script>
import maintain from "@/mixins/maintain";

export default {
  name: "maintainRow1Right",
  mixins: [maintain],
  data() {
    return {
      attrCorrespondence: {
        类型: "type",
        品类: "category",
        "6月": "june",
        "7月": "july",
        "8月": "august",
        "9月": "september",
        "10月": "october",
        "11月": "november",
        "12月": "december"
      },
      totalData: 50,
      initData: [],
      tableData: []
    };
  },
  computed: {},
  watch: {
    initData: {
      deep: true,
      handler(newValue, oldValue) {
        let returnData = [];
        if (!this.$lodash.isEmpty(newValue)) {
          newValue.forEach((item, index) => {
            let tempObj = {};
            this.$lodash.forIn(item, (value, key) => {
              tempObj[this.attrCorrespondence[key.trim()]] = value;
            });
            this.$store.state.row1RightCount++;
            tempObj.id = this.$store.state.row1RightCount;
            returnData.push(tempObj);
          });
        }
        console.log(returnData);
        this.tableData = this.tableData.concat(returnData);
        this.cacheData = this.tableData.map(item => ({ ...item }));
      }
    },
    tableData: {
      deep: true,
      handler(newValue, oldValue) {
        // this.cacheData = newValue.map(item => ({ ...item }));
        this.$store.state.row1RightBackupData = newValue;
      }
    }
  },
  methods: {
    addData() {
      this.$store.state.row1RightCount++;
      this.tableData.push({
        id: this.$store.state.row1RightCount,
        type: "请修改",
        category: "请修改",
        june: "请修改",
        july: "请修改",
        august: "请修改",
        september: "请修改",
        october: "请修改",
        november: "请修改",
        december: "请修改"
      });
      this.cacheData = this.tableData.map(item => ({ ...item }));
    }
  }
};
</script>

<style lang="scss" scoped>
.el-header {
  background-color: #fafafa;
  border: 1px solid #eaeefb;
  color: #333;
  text-align: center;
  display: flex;
  align-items: center;
}
.el-main {
  background-color: #fafafa;
  color: #333;
  text-align: center;
}
.margin-right {
  margin-right: 10px;
}
.save-button,
.cancel-button {
  color: #1890ff;
  cursor: pointer;
  padding: 0 5px;
  font-size: 12px;
}
</style>