          </template>
        </el-table-column>
        <el-table-column label="近八周折扣">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <el-input size="small" v-model="scope.row.discounts8" placeholder="请输入内容"></el-input>
            </span>
            <span v-else>{{scope.row.discounts8}}</span>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="180">
          <template slot-scope="scope">
            <span v-if="scope.row.editable">
              <a class="save-button" @click="save(scope.row.id)">保存</a>
              <a class="cancel-button" @click="cancel(scope.row.id)">取消</a>
            </span>
            <el-button v-else size="mini" @click="handleEdit(scope.row.id)">编辑</el-button>
            <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalData"
      ></el-pagination>-->
    </el-main>
  </el-container>
</template>

<script>
import maintain from "@/mixins/maintain";

export default {
  name: "maintainRow1Left",
  mixins: [maintain],
  data() {
    return {
      attrCorrespondence: {
        渠道: "channel",
        销售达成: "salesAchievement",
        库存供应: "inventorySupply",
        近一周销售: "sales11",
        近二周销售: "sales12",
        近三周销售: "sales13",
        近四周销售: "sales14",
        近五周销售: "sales15",
        近六周销售: "sales16",
        近七周销售: "sales17",
        近八周销售: "sales18",
        近一周折扣: "discounts1",
        近二周折扣: "discounts2",
        近三周折扣: "discounts3",
        近四周折扣: "discounts4",
        近五周折扣: "discounts5",
        近六周折扣: "discounts6",
        近七周折扣: "discounts7",
        近八周折扣: "discounts8"
      },
      totalData: 50,
      initData: [],
      tableData: []
    };
  },
  computed: {},
  watch: {
    initData: {
      deep: true,
      handler(newValue, oldValue) {
        let returnData = [];
        if (!this.$lodash.isEmpty(newValue)) {
          newValue.forEach((item, index) => {
            let tempObj = {};
            this.$lodash.forIn(item, (value, key) => {
              if (this.attrCorrespondence[key.trim()].indexOf("sales1") > -1) {
                tempObj[
                  this.attrCorrespondence[key.trim()]
                ] = this.$lodash.round(value / 10000);
              } else if (
                this.attrCorrespondence[key.trim()].indexOf("discounts") > -1
              ) {
                tempObj[
                  this.attrCorrespondence[key.trim()]
                ] = this.$lodash.round(value, 2);
              } else {
                tempObj[
                  this.attrCorrespondence[key.trim()]
                ] = this.$lodash.isNumber(value)
                  ? this.$lodash.round(value, 2)
                  : value;
              }
            });
            this.$store.state.row1LeftCount++;
            tempObj.id = this.$store.state.row1LeftCount;
            returnData.push(tempObj);
          });
        }
        console.log(returnData);
        this.tableData = this.tableData.concat(returnData);
        this.cacheData = this.tableData.map(item => ({ ...item }));
      }
    },
    tableData: {
      deep: true,
      handler(newValue, oldValue) {
        // this.cacheData = newValue.map(item => ({ ...item }));
        this.$store.state.row1LeftBackupData = newValue;
      }
    }
  },
  methods: {
    addData() {
      this.$store.state.row1LeftCount++;
      this.tableData.push({
        id: this.$store.state.row1LeftCount,
        channel: "请修改",
        salesAchievement: "请修改",
        inventorySupply: "请修改",
        sales11: "请修改",
        sales12: "请修改",
        sales13: "请修改",
        sales14: "请修改",
        sales15: "请修改",
        sales16: "请修改",
        sales17: "请修改",
        sales18: "请修改",
        discounts1: "请修改",
        discounts2: "请修改",
        discounts3: "请修改",
        discounts4: "请修改",
        discounts5: "请修改",
        discounts6: "请修改",
        discounts7: "请修改",
        discounts8: "请修改"
      });
      this.cacheData = this.tableData.map(item => ({ ...item }));
    }
  }
};
</script>

<style lang="scss" scoped>
.el-header {
  background-color: #fafafa;
  border: 1px solid #eaeefb;
  color: #333;
  text-align: center;
  display: flex;
  align-items: center;
}
.el-main {
  background-color: #fafafa;
  color: #333;
  text-align: center;
}
.margin-right {
  margin-right: 10px;
}
.save-button,
.cancel-button {
  color: #1890ff;
  cursor: pointer;
  padding: 0 5px;
  font-size: 12px;
}
</style>