<template>
  <div class="row-1_left">
    <div class="data-title">
      <div class="data-title_text">销售预测</div>
      <div class="data-legend">
        <div class="row-1-left_legend-1">销售</div>
        <div class="row-1-left_legend-2">折扣</div>
      </div>
    </div>
    <div class="data-content">
      <pieAndLineChart v-for="(item, index) in tempData" :key="index" :propsData="item"/>
    </div>
  </div>
</template>

<script>
import pieAndLineChart from "./row1leftcomponents/pieAndLineChart.vue";

export default {
  name: "Row1Left",
  data() {
    return {};
  },
  components: {
    pieAndLineChart
  },
  computed: {
    tempData() {
      let data = this.$store.state.row1LeftPublishData,
        tempArry = [];
      if (!this.$lodash.isEmpty(data)) {
        data.forEach((element, index) => {
          tempArry.push({
            name: element.channel,
            id: index + 1,
            leftpie: {
              name: "销售达成",
              value: [
                element.salesAchievement * 100,
                100 - element.salesAchievement * 100
              ]
            },
            midline: {
              name: "销售折扣",
              value: [
                [
                  element.sales11,
                  element.sales12,
                  element.sales13,
                  element.sales14,
                  element.sales15,
                  element.sales16,
                  element.sales17,
                  element.sales18
                ],
                [
                  element.discounts1,
                  element.discounts2,
                  element.discounts3,
                  element.discounts4,
                  element.discounts5,
                  element.discounts6,
                  element.discounts7,
                  element.discounts8
                ]
              ]
            },
            rightpie: {
              name: "库存供应",
              value: [
                element.inventorySupply * 100,
                100 - element.inventorySupply * 100
              ]
            }
          });
        });
        console.log(tempArry);
      }
      return tempArry;
    }
  },
  watch: {
    // "$store.state.row1LeftPublishData": {
    //   deep: true,
    //   handler(newValue, oldValue) {
    //     console.log(newValue);
    //   }
    // }
  }
};
</script>

<style lang="scss" scoped>
.row-1_left {
  width: 603px;
  height: 323px;
  padding: 12px;
  background-color: #0a0819;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  //   border: 1px solid #100b30;
  // opacity: 0.8;
  border-radius: 7px;
  box-shadow: inset 0px 0px 20px #2b255f;
}
.data-title {
  // margin: 12px 12px 0 12px;
  display: flex;
  justify-content: space-between;
}
.data-title_text {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  border-left: 6px solid #3b54d3;
  padding-left: 12px;
}
.data-legend {
  display: flex;
  justify-content: center;
}
.row-1-left_legend-1 {
  font-size: 16px;
  font-weight: 400;
  color: #7c83a8;
  margin-left: 48px;
  position: relative;
}
.row-1-left_legend-1::before {
  width: 13px;
  height: 0;
  border-top: 4px solid #3b8bff;
  position: absolute;
  content: "";
  top: 8px;
  left: -20px;
}
.row-1-left_legend-2 {
  font-size: 16px;
  font-weight: 400;
  color: #7c83a8;
  margin-left: 48px;
  position: relative;
}
.row-1-left_legend-2::before {
  width: 13px;
  height: 0;
  border-top: 4px solid #fcc63f;
  position: absolute;
  content: "";
  top: 8px;
  left: -20px;
}
.data-content {
  margin-top: 16px;
}
</style>