<template>
  <div :class="typeclassname">
    <div class="plan-title">{{ordername}}</div>
    <div class="plan-content">
      <div class="plan-time-span">
        <div
          :class="['time-span-item', timespanclassname]"
          :style="{ width: timespanstyles }"
          v-for="(item, index) in timespandata"
          :key="index"
        >{{item.name}}</div>
      </div>
      <div class="plan-order-distribution">
        <div
          v-for="(item, index) in processedorderdata.orderData"
          :key="`order${index}`"
          :class="['order-item',item.classname]"
          :style="item.styleObject"
        >{{parseFloat(item.styleObject.width) > 1.4 ? item.ordernumber : ''}}</div>
        <div
          v-for="(item, index) in processedorderdata.productionProgressData"
          :key="`processe${index}`"
          :class="['order-status-item',item.classname]"
          :style="item.styleObject"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "orderBar",
  props: ["propsData"],
  data() {
    return {
      correspondence: {
        1: this.getQuarterlyDays(),
        2: this.$moment().daysInMonth(),
        3: 7
      },
      typeClassMap: {
        1: "quarterly-plan",
        2: "monthly-plan",
        3: "weekly-plan"
      },
      timeClassMap: {
        1: "quarterly-item",
        2: "",
        3: "weekly-item"
      },
      timeStyleMap: {
        1: "",
        2:
          this.$lodash.floor(
            this.getVW(603) / this.$moment().daysInMonth(),
            5
          ) + "vw",
        3: ""
      },
      orderClassMap: {
        completed: "order-completed",
        extension: "order-extension",
        processing: "order-processing",
        prepare: "order-prepare"
      },
      progressClassMap: {
        completed: "progress-completed",
        extension: "progress-extension",
        processing: "progress-processing",
        prepare: "progress-prepare"
      }
    };
  },
  computed: {
    typeclassname: function() {
      return this.typeClassMap[this.propsData.flag];
    },
    ordername: function() {
      return this.propsData.name;
    },
    timespanclassname: function() {
      return this.timeClassMap[this.propsData.flag];
    },
    timespanstyles: function() {
      return this.timeStyleMap[this.propsData.flag];
    },
    timespandata: function() {
      return this.propsData.timespan;
    },
    processedorderdata: function() {
      let initialData = this.propsData.data,
        temparry = {
          orderData: [],
          productionProgressData: []
        },
        leftDay = this.propsData.timespan[0].startDay;
      if (!this.$lodash.isEmpty(initialData)) {
        initialData.forEach((element, index) => {
          let predictStartTime = element.predictStartTime,
            predictEndTime = element.predictEndTime,
            actualStartTime = element.actualStartTime,
            actualEndTime = element.actualEndTime,
            orderStatus = element.orderStatus,
            orderNumber = element.orderNumber,
            showIndex = element.index,
            progressStyle = "";
          let orderDiffLeftDay = this.$lodash.floor(
            this.$moment(predictStartTime).diff(
              this.$moment(leftDay),
              "days",
              true
            ),
            3
          );
          let orderDiffDays = this.$lodash.floor(
            this.$moment(predictEndTime).diff(
              this.$moment(predictStartTime),
              "days",
              true
            ),
            3
          );
          let orderTopPx = (showIndex + 1) * 4 + showIndex * (11 + 1);
          if (orderStatus == "processing") {
            let nowTime = this.$moment();
            if (nowTime.isBetween(actualStartTime, actualEndTime)) {
              let molecule = nowTime.diff(
                this.$moment(actualStartTime),
                "days",
                true
              );
              let denominator = this.$moment(actualEndTime).diff(
                this.$moment(actualStartTime),
                "days",
                true
              );
              let scale =
                this.$lodash.floor((molecule / denominator) * 100) + "%";
              progressStyle = `linear-gradient(to right, #3b8bff ${scale}, #8f48ff 25%)`;
            }
          }
          let progressDiffLeftDay = this.$lodash.floor(
            this.$moment(actualStartTime).diff(
              this.$moment(leftDay),
              "days",
              true
            ),
            3
          );
          let progressDiffDays = this.$lodash.floor(
            this.$moment(actualEndTime).diff(
              this.$moment(actualStartTime),
              "days",
              true
            ),
            3
          );
          let progressTopPx = (showIndex + 1) * 4 + showIndex * (11 + 1) + 11;
          temparry.orderData.push({
            ordernumber: orderNumber,
            classname: this.orderClassMap[orderStatus],
            styleObject: {
              width:
                this.$lodash.floor(this.getdayWidth(orderDiffDays), 5) + "vw",
              top: this.$lodash.floor(this.getVW(orderTopPx), 5) + "vw",
              left:
                this.$lodash.floor(this.getdayWidth(orderDiffLeftDay), 5) + "vw"
            }
          });
          temparry.productionProgressData.push({
            classname: this.progressClassMap[orderStatus],
            styleObject: {
              width:
                this.$lodash.floor(this.getdayWidth(progressDiffDays), 5) +
                "vw",
              top: this.$lodash.floor(this.getVW(progressTopPx), 5) + "vw",
              left:
                this.$lodash.floor(this.getdayWidth(progressDiffLeftDay), 5) +
                "vw",
              background: progressStyle
            }
          });
          // console.log(temparry);
        });
      }
      return temparry;
    }
  },
  mounted() {},
  methods: {
    getdayWidth(days) {
      let correspondence = this.correspondence,
        $px = 603,
        $vw_base = 1920;
      return (
        ((($px / $vw_base) * 100) / correspondence[this.propsData.flag]) * days
      );
    },
    getQuarterlyDays() {
      let currentMonth = this.$moment().format("YYYY-MM"),
        days = 0;
      for (let i = 0; i < 9; i++) {
        days += this.$moment(
          this.$moment(currentMonth).add(i, "months")
        ).daysInMonth();
      }
      return days;
    },
    getVW($px) {
      let $vw_base = 1920;
      return ($px / $vw_base) * 100;
    }
  }
};
</script>

<style lang="scss" scoped>
.plan-title {
  font-size: 12px;
  font-weight: 400;
  color: #7c83a8;
}
.plan-time-span {
  width: 603px;
  height: 18px;
  line-height: 18px;
  margin-top: 6px;
  background: #0e0e25;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.time-span-item {
  font-size: 12px;
  font-weight: 400;
  color: #3b54d3;
  text-align: center;
}
.monthly-plan,
.weekly-plan {
  margin-top: 6px;
}
.quarterly-item {
  width: calc(603px / 9);
}
.monthly-item {
  width: calc(603px / 4);
}
.weekly-item {
  width: calc(603px / 7);
}
.plan-order-distribution {
  width: 603px;
  height: 58px;
  position: relative;
  overflow: hidden;
}
.order-item {
  height: 11px;
  font-size: 8px;
  font-weight: 400;
  position: absolute;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.order-status-item {
  height: 1px;
  position: absolute;
}
.order-completed,
.order-extension {
  background-color: #1c183b;
  color: #a49fca;
}
.order-processing {
  background-color: #fcc63f;
  color: #7c5f17;
}
.order-prepare {
  background-color: #3b8bff;
  color: #16396c;
}
.progress-completed {
  background: #2dff81;
}
.progress-extension {
  background: #fe3253;
}
.progress-processing {
  // background: linear-gradient(to right, #3b8bff 25%, #8f48ff 25%);
  background: #8f48ff;
}
.progress-prepare {
  background: #8f48ff;
}
</style>