<template>
  <div class="content_row">
    <div class="content_row-pie" :id="pieleftchartid"></div>
    <div class="content_row-line" :id="linechartid"></div>
    <div class="content_row-pie" :id="pierightchartid"></div>
  </div>
</template>

<script>
export default {
  name: "pieAndLineChart",
  props: ["propsData"],
  computed: {
    pieleftchartid: function() {
      return `row1leftpieone${this.propsData.id}`;
    },
    linechartid: function() {
      return `row1leftline${this.propsData.id}`;
    },
    pierightchartid: function() {
      return `row1leftpietwo${this.propsData.id}`;
    },
    chartname: function() {
      return this.propsData.name;
    },
    pieleftchartdata: function() {
      return this.propsData.leftpie;
    },
    linechartdata: function() {
      return this.propsData.midline;
    },
    pierightchartdata: function() {
      return this.propsData.rightpie;
    }
  },
  data() {
    return {
      renderChartFlag: true
    };
  },
  mounted() {},
  watch: {
    propsData: {
      deep: true,
      handler(newValue, oldValue) {
        this.renderChartFlag = true;
      }
    },
    "$store.state.currentComponent": {
      deep: true,
      handler(newValue, oldValue) {
        if (newValue == "Home" && this.renderChartFlag)
          this.renderPieLeft(this.pieleftchartid),
            this.renderLine(this.linechartid),
            this.renderPieRight(this.pierightchartid);
      }
    }
  },
  methods: {
    renderPieLeft(chartid) {
      var myPie = this.$echarts.init(document.getElementById(chartid));
      var option = {
        // legend: {
        //   orient: "vertical",
        //   x: "left",
        //   data: ["直接访问", "邮件营销", "联盟广告", "视频广告", "搜索引擎"]
        // },
        series: [
          {
            name: `${this.chartname}`,
            type: "pie",
            radius: ["70%", "100%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center",
                textStyle: {
                  fontSize: "12"
                }
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "12",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: this.pieleftchartdata.value[0],
                name: `${this.pieleftchartdata.name}`,
                label: {
                  show: true,
                  color: "#1CC9E8",
                  // formatter: "{d}% \n {b}",
                  formatter: ["{a|{d}%}", "{b|{b}}"].join("\n"),
                  rich: {
                    a: {
                      color: "#1CC9E8",
                      fontSize: 18,
                      fontFamily: "digital"
                      // lineHeight: 10
                    },
                    b: {
                      color: "#7C83A8"
                      // height: 40
                    }
                  }
                },
                itemStyle: {
                  color: "#3B54D3"
                }
              },
              {
                value: this.pieleftchartdata.value[1],
                name: "空白",
                itemStyle: {
                  color: "#0a091a",
                  borderColor: "#243079"
                }
              }
            ]
          }
        ]
      };
      myPie.setOption(option, true);
    },
    renderLine(chartid) {
      var myPie = this.$echarts.init(document.getElementById(chartid));
      var option = {
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "1%",
          right: "1%",
          top: "20%",
          bottom: "1%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          data: ["6/1", "6/8", "6/15", "6/22", "6/29", "7/6", "7/13", "7/20"],
          axisTick: {
            show: false
          },
          axisLabel: {
            color: "#3A3E5E"
          },
          axisLine: {
            lineStyle: {
              color: "#120F29",
              fontWeight: "400"
            }
          }
        },
        yAxis: [
          {
            type: "value",
            nameLocation: "end",
            nameGap: 5,
            nameTextStyle: {
              color: "#3A3E5E"
            },
            axisLine: {
              lineStyle: {
                color: "#120F29"
              }
            },
            splitLine: {
              lineStyle: {
                color: "#120F29"
              }
            },
            // minInterval: 1,
            name: "万",
            axisTick: {
              show: false
            },
            axisLabel: {
              margin: 2,
              color: "#3A3E5E",
              fontWeight: "400"
            }
          },
          {
            type: "value",
            nameLocation: "end",
            nameGap: 5,
            nameTextStyle: {
              color: "#3A3E5E"
            },
            axisLine: {
              lineStyle: {
                color: "#120F29"
              }
            },
            splitLine: {
              lineStyle: {
                color: "#120F29"
              }
            },
            // minInterval: 0.2,
            name: "率",
            axisTick: {
              show: false
            },
            axisLabel: {
              margin: 2,
              color: "#3A3E5E",
              fontWeight: "400"
            }
          }
        ],
        series: [
          {
            name: "销售",
            type: "line",
            lineStyle: {
              color: "#3B8BFF"
            },
            itemStyle: {
              color: "#ffffff",
              borderColor: "#3B8BFF"
            },
            data: this.linechartdata.value[0]
          },
          {
            name: "折扣",
            type: "line",
            yAxisIndex: 1,
            lineStyle: {
              color: "#FCC63F"
            },
            itemStyle: {
              color: "#ffffff",
              borderColor: "#FCC63F"
            },
            data: this.linechartdata.value[1]
          }
        ]
      };
      myPie.setOption(option, true);
    },
    renderPieRight(chartid) {
      var myPie = this.$echarts.init(document.getElementById(chartid));
      var option = {
        // legend: {
        //   orient: "vertical",
        //   x: "left",
        //   data: ["直接访问", "邮件营销", "联盟广告", "视频广告", "搜索引擎"]
        // },
        series: [
          {
            name: `${this.chartname}`,
            type: "pie",
            radius: ["70%", "100%"],
            hoverAnimation: false,
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center",
                textStyle: {
                  fontSize: "12"
                }
              },
              emphasis: {
                show: false,
                textStyle: {
                  fontSize: "12",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            data: [
              {
                value: this.pierightchartdata.value[0],
                name: `${this.pierightchartdata.name}`,
                label: {
                  show: true,
                  color: "#1CC9E8",
                  // formatter: "{d}% \n {b}",
                  formatter: ["{a|{d}%}", "{b|{b}}"].join("\n"),
                  rich: {
                    a: {
                      color: "#1CC9E8",
                      fontSize: 18,
                      fontFamily: "digital"
                      // lineHeight: 10
                    },
                    b: {
                      color: "#7C83A8"
                      // height: 40
                    }
                  }
                },
                itemStyle: {
                  color: "#FF9E40"
                }
              },
              {
                value: this.pierightchartdata.value[1],
                name: "空白",
                itemStyle: {
                  color: "#0a091a",
                  borderColor: "#243079"
                }
              }
            ]
          }
        ]
      };
      myPie.setOption(option, true);
      this.renderChartFlag = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@font-face {
  font-family: "digital";
  src: url("../../assets/digital-7.ttf");
}
.content_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 17px;
}
.content_row-pie {
  width: 115px;
  height: 115px;
  // border-radius: 50%;
  // background-color: #3fbcfd;
}
.content_row-line {
  width: 353px;
  height: 135px;
  // background-color: #120f29;
}
</style>