<template>
  <div class="row-2_left">
    <div class="general-title">滞销款</div>
    <div class="left-data">
      <lineChart v-for="(item, index) in tempData" :key="index" :propsData="item"/>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import lineChart from "./row2leftcomponents/lineChart.vue";

export default {
  name: "Row2Left",
  data() {
    return {};
  },
  components: {
    lineChart
  },
  computed: {
    tempData() {
      let data = this.$store.state.row2LeftPublishData,
        tempArry = [];
      if (!this.$lodash.isEmpty(data)) {
        let tempObj = {},
          i = 0;
        data.forEach((element, index) => {
          let styleName = element.styleNumber;
          let styleExist = this.$lodash.has(tempObj, styleName);
          if (styleExist) {
            tempObj[styleName].push(element);
          } else {
            tempObj[styleName] = [element];
          }
        });
        this.$lodash.forIn(tempObj, (value, key) => {
          let salesFigures = value.filter(item => item.type === "销售数")[0],
            salesPromotion1 = value.filter(
              item => item.type === "销售数促销1"
            )[0],
            salesPromotion2 = value.filter(
              item => item.type === "销售数促销2"
            )[0],
            salesPromotion3 = value.filter(
              item => item.type === "销售数促销3"
            )[0],
            monthlyEndInventory = value.filter(
              item => item.type === "月末库存"
            )[0],
            monthlyEndInventory1 = value.filter(
              item => item.type === "月末库存促销1"
            )[0],
            monthlyEndInventory2 = value.filter(
              item => item.type === "月末库存促销2"
            )[0],
            monthlyEndInventory3 = value.filter(
              item => item.type === "月末库存促销3"
            )[0];
          tempArry.push({
            id: ++i,
            styleNumber: key,
            ratio: `${value[0].ratio}`,
            data: [
              [
                salesFigures.may,
                salesFigures.june,
                salesFigures.july,
                salesFigures.august,
                salesFigures.september,
                salesFigures.october,
                salesFigures.november,
                salesFigures.december,
                salesFigures.january,
                salesFigures.february,
                salesFigures.march
              ],
              [
                salesPromotion1.may,
                salesPromotion1.june,
                salesPromotion1.july,
                salesPromotion1.august,
                salesPromotion1.september,
                salesPromotion1.october,
                salesPromotion1.november,
                salesPromotion1.december,
                salesPromotion1.january,
                salesPromotion1.february,
                salesPromotion1.march
              ],
              [
                salesPromotion2.may,
                salesPromotion2.june,
                salesPromotion2.july,
                salesPromotion2.august,
                salesPromotion2.september,
                salesPromotion2.october,
                salesPromotion2.november,
                salesPromotion2.december,
                salesPromotion2.january,
                salesPromotion2.february,
                salesPromotion2.march
              ],
              [
                salesPromotion3.may,
                salesPromotion3.june,
                salesPromotion3.july,
                salesPromotion3.august,
                salesPromotion3.september,
                salesPromotion3.october,
                salesPromotion3.november,
                salesPromotion3.december,
                salesPromotion3.january,
                salesPromotion3.february,
                salesPromotion3.march
              ],
              [
                monthlyEndInventory.may,
                monthlyEndInventory.june,
                monthlyEndInventory.july,
                monthlyEndInventory.august,
                monthlyEndInventory.september,
                monthlyEndInventory.october,
                monthlyEndInventory.november,
                monthlyEndInventory.december,
                monthlyEndInventory.january,
                monthlyEndInventory.february,
                monthlyEndInventory.march
              ],
              [
                monthlyEndInventory1.may,
                monthlyEndInventory1.june,
                monthlyEndInventory1.july,
                monthlyEndInventory1.august,
                monthlyEndInventory1.september,
                monthlyEndInventory1.october,
                monthlyEndInventory1.november,
                monthlyEndInventory1.december,
                monthlyEndInventory1.january,
                monthlyEndInventory1.february,
                monthlyEndInventory1.march
              ],
              [
                monthlyEndInventory2.may,
                monthlyEndInventory2.june,
                monthlyEndInventory2.july,
                monthlyEndInventory2.august,
                monthlyEndInventory2.september,
                monthlyEndInventory2.october,
                monthlyEndInventory2.november,
                monthlyEndInventory2.december,
                monthlyEndInventory2.january,
                monthlyEndInventory2.february,
                monthlyEndInventory2.march
              ],
              [
                monthlyEndInventory3.may,
                monthlyEndInventory3.june,
                monthlyEndInventory3.july,
                monthlyEndInventory3.august,
                monthlyEndInventory3.september,
                monthlyEndInventory3.october,
                monthlyEndInventory3.november,
                monthlyEndInventory3.december,
                monthlyEndInventory3.january,
                monthlyEndInventory3.february,
                monthlyEndInventory3.march
              ]
            ]
          });
        });
        console.log(tempObj);
      }
      return tempArry;
    }
  },
  watch: {
    "$store.state.row2LeftPublishData": {
      deep: true,
      handler(newValue, oldValue) {
        console.log(newValue);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.row-2_left {
  width: 603px;
  height: 323px;
  padding: 12px;
  background-color: #0a0819;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 7px;
  box-shadow: inset 0px 0px 20px #2b255f;
}
.general-title {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  border-left: 6px solid #3b54d3;
  padding-left: 12px;
}
.left-data {
  margin-top: 12px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}
</style>