<template>
  <div class="content-item">
    <div class="content-item_left">
      <span class="item_left-title">{{chartname}}</span>
      <div class="item_left-data" :id="treemapchartid"></div>
    </div>
    <div class="content-item_right" :id="linechartid"></div>
  </div>
</template>

<script>
export default {
  name: "treemapAndLineChart",
  props: ["propsData"],
  data() {
    return {
      renderChartFlag: true
    };
  },
  computed: {
    treemapchartid: function() {
      return `row1righttreemap${this.propsData.id}`;
    },
    linechartid: function() {
      return `row1rightline${this.propsData.id}`;
    },
    chartname: function() {
      return this.propsData.name;
    },
    linechartdata: function() {
      return this.propsData.data;
    }
  },
  mounted() {
    // this.renderTreemap(this.treemapchartid);
    // this.renderLine(this.linechartid);
  },
  watch: {
    propsData: {
      deep: true,
      handler(newValue, oldValue) {
        this.renderChartFlag = true;
      }
    },
    "$store.state.currentComponent": {
      deep: true,
      handler(newValue, oldValue) {
        if (newValue == "Home" && this.renderChartFlag)
          this.renderTreemap(this.treemapchartid),
            this.renderLine(this.linechartid);
      }
    }
  },
  methods: {
    renderLine(chartid) {
      var myPie = this.$echarts.init(document.getElementById(chartid));
      var option = {
        tooltip: {
          trigger: "axis"
          // position: ["50%", "50%"]
        },
        grid: {
          left: 0,
          right: "2%",
          top: 2,
          bottom: "-4%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          data: ["6/1", "7/1", "8/1", "9/1", "10/1", "11/1", "12/1"],
          axisTick: {
            show: false
          },
          axisLabel: {
            margin: 2,
            color: "#3A3E5E"
          },
          axisLine: {
            lineStyle: {
              color: "#120F29",
              fontWeight: "400"
            }
          },
          splitLine: {
            show: true,
            interval: 0,
            lineStyle: {
              color: "#120F29"
            }
          }
        },
        yAxis: {
          type: "value",
          nameLocation: "end",
          nameGap: 5,
          nameTextStyle: {
            color: "#3A3E5E"
          },
          axisLine: {
            lineStyle: {
              color: "#120F29"
            }
          },
          splitLine: {
            lineStyle: {
              color: "#120F29"
            }
          },
          minInterval: 2000,
          axisTick: {
            show: false
          },
          axisLabel: {
            margin: 2,
            color: "#3A3E5E",
            fontWeight: "400"
          }
        },
        series: [
          {
            name: `${this.linechartdata[0].name}`,
            type: "line",
            lineStyle: {
              color: "#FCC63F"
            },
            smooth: true,
            itemStyle: {
              opacity: 0,
              color: "#ffffff",
              borderColor: "#3B8BFF"
            },
            data: this.linechartdata[0].value
          },
          {
            name: `${this.linechartdata[1].name}`,
            type: "line",
            lineStyle: {
              color: "#3B8BFF"
            },
            smooth: true,
            itemStyle: {
              opacity: 0,
              color: "#ffffff",
              borderColor: "#FCC63F"
            },
            data: this.linechartdata[1].value
          }
        ]
      };
      myPie.setOption(option, true);
      this.renderChartFlag = false;
    },
    renderTreemap(chartid) {
      var myPie = this.$echarts.init(document.getElementById(chartid));
      var option = {
        tooltip: {
          trigger: "item"
        },
        series: [
          {
            type: "treemap",
            // tooltip: {
            //   formatter: "{a}"
            // },
            left: 0,
            top: 0,
            right: 0,
            bottom: 0,
            roam: false,
            nodeClick: false,
            // silent: true,
            label: {
              show: false
            },
            breadcrumb: {
              show: false
            },
            data: [
              {
                name: "nodeAa", // First leaf of first tree
                value: 4,
                itemStyle: {
                  color: "#3B8AFF"
                }
              },
              {
                name: "nodeAb", // Second leaf of first tree
                value: 6,
                itemStyle: {
                  color: "#6CA7FF"
                }
              },
              {
                name: "nodeAb", // Second leaf of first tree
                value: 6,
                itemStyle: {
                  color: "#93BFFF"
                }
              }
            ]
          }
        ]
      };
      myPie.setOption(option, true);
    }
  }
};
</script>

<style lang="scss" scoped>
.content-item {
  width: 280px;
  height: 90px;
  padding: 0 10px;
  margin-bottom: 11px;
  display: flex;
  justify-content: space-between;
}
.content-item_left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.item_left-title {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  color: #7c83a8;
  text-align: center;
}
.item_left-data {
  width: 76px;
  height: 76px;
  background-color: #93bfff;
}
.content-item_right {
  width: 200px;
  height: 90px;
  // background-color: #f5b125;
}
</style>